import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// @material-ui/core components
import PropTypes from "prop-types";
import { makeStyles } from "@material-ui/core/styles";
// core components
import Header from "components/Header/Header.js";
import Footer from "components/Footer/Footer.js";
import HeaderLinks from "components/Header/HeaderLinks.js";
import Parallax from "components/Parallax/Parallax.js";

import styles from "assets/jss/material-kit-react/views/profilePage.js";

const useStyles = makeStyles(styles);

export default function OpenSeaIframePage(props) {
  const classes = useStyles();
  const { ...rest } = props;
  const urlPath = props.match.path;

  let openSeaUrl = "https://opensea.io/assets";
  openSeaUrl += urlPath == "/collections" ? "" : urlPath;
  openSeaUrl += "?embed=true&ref=0xFa4c9487c14Ad54f0A29f480D788f0B73Eeb8894";
  console.log("UrlPath", urlPath, openSeaUrl);

  return (
    <div>
      <Header
        color="transparent"
        brand="iEthereum NFT Marketplace"
        rightLinks={<HeaderLinks />}
        fixed
        changeColorOnScroll={{
          height: 100,
          color: "white",
        }}
        {...rest}
      />
      <Parallax
        small
        filter
        image={require("assets/img/profile-bg.jpg").default}
      />
      <div className={classNames(classes.main, classes.mainRaised)}>
        <iframe
          src={openSeaUrl}
          width="100%"
          frameBorder="0"
          allowFullScreen
          style={{ minHeight: "92vh" }}
        />
      </div>
      <Footer />
    </div>
  );
}

OpenSeaIframePage.propTypes = {
  match: PropTypes.object,
};
