import React from "react";
import { render } from "react-snapshot";
import { createBrowserHistory } from "history";
import { Router, Route, Switch } from "react-router-dom";

import "assets/scss/material-kit-react.scss?v=1.10.0";

// pages for this product
import LandingPage from "views/LandingPage/LandingPage";
import OpenSeaIframePage from "views/OpenSeaIframePage/OpenSeaIframePage";

var hist = createBrowserHistory();

render(
  <Router history={hist}>
    <Switch>
      <Route path="/collections" component={OpenSeaIframePage} />
      <Route path="/sneakr-token" component={OpenSeaIframePage} />
      <Route path="/crypto-bull-society" component={OpenSeaIframePage} />
      <Route path="/superrare" component={OpenSeaIframePage} />
      <Route path="/thelittlesnft" component={OpenSeaIframePage} />
      <Route path="/" component={LandingPage} />
    </Switch>
  </Router>,
  document.getElementById("root")
);
